@import url(https://fonts.googleapis.com/css?family=Roboto:500);
/* .App {
  text-align: center;
  padding: 2% 5%;
} */

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

.city {
  width: 250px;
}

.CitySearch {
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  max-width: 300px;
  /* margin: auto; */
  margin: 1rem auto;
  position: relative;
}

.suggestions {
  width: 250px;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 0;
  top: 43px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}

.event {
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  text-align: center;
  border: 2px solid #392a1d;
  border-radius: 15px;
  position: relative;
}

.event-details {
  padding-top: 5px;
  background-color: #b1dcbd;
  color: #000;
}

a {
  text-decoration: none;
}

a:hover {
  color: #fff;
}

.image-wrapper {
  margin-left: 60%;
  margin-top: 2rem;
}

.details-button {
  /* position: absolute; */
  /* right: 5px; */
  margin-top: 10px;
  /* bottom: 5px; */
  padding: 5px 8px;
  border-radius: 4px;
  background-color: #fff;
  outline: none;
  cursor: pointer;
  font-weight: bold;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.numberOfEvents {
  width: 200px;
  margin: 20px auto;
  text-align: center;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

.Alert p{
  position: fixed;
  top: 10px;
  display: flex;
  margin: 0 auto;
}

.data-vis-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: #fcbf8d;
  text-align: center;
  padding: 2% 5%;
}
.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: flex;
}
.recharts-surface {
  overflow: visible;
}

.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  padding: 20px;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}

.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
  background: #1669f2;
}
.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}
.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}

.list-wrapper {
  text-align: center;
  padding: 2% 5%;
}
.list-h2 {
  background-color: #c9daf5;
  border: 2px solid #1669f2;
  line-height: 4rem;
  border-radius: 20px;
  width: 400px;
  margin: 2rem auto;
  color: #000;
  text-align: center;
  /* padding: 2% 5%; */
}

.data-h2 {
  border-radius: 20px;
  /* width: 30rem; */
  /* line-height: 4rem; */
  /* position: absolute; */
  /* height: 20rem; */
  /* margin: 5rem auto; */
  /* margin-right: 70%; */
  background-color: #fff;
  border: #f07f85 1px solid;
}
h2 {
  text-align: center;
}


@media only screen and (min-width: 992px) {
  .data-vis-wrapper {
    flex-direction: row;
  }
  .data-vis-wrapper .recharts-responsive-container {
    flex-basis: 50%;
    max-width: 50%;
    min-width: auto;
    margin: 0 auto;
  }
}

